* {
  box-sizing: border-box;
}

body {
  color: #131212;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.container {
  width: 1024px;
  min-height: 768px;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
}

.breed-select {
  height: 40px;
  min-width: 240px;
  color: pink;
  text-align: center;
  border: 2px solid #000;
  border-radius: 10px;
  margin-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
}

select:focus {
  outline-color: pink;
}

.cat-info {
  gap: 20px;
  display: flex;
}

img {
  max-width: 500px;
  max-height: 500px;
}

h2 {
  margin-top: 0;
}

.temperament {
  margin-right: 5px;
  font-weight: 700;
}

.loader {
  color: pink;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:before {
  content: "";
  animation: print 2s linear infinite alternate;
}

.loader:after {
  content: "";
  width: 2px;
  height: 1.3em;
  opacity: .8;
  background: currentColor;
  animation: blink 1s steps(2, end) infinite;
  position: absolute;
  top: 50%;
  right: -4px;
  transform: translateY(-45%);
}

@keyframes blink {
  0% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}

@keyframes print {
  100% {
    content: "Loading...";
  }
}

